/*========== Basic ==========*/
html {
  touch-action: manipulation;
  height: 100%; }

* {
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-drag: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none;
  min-width: 0px;
  min-height: 0px; }
  *:before, *:after {
    box-sizing: border-box; }

button, input, textarea, select {
  outline: none;
  display: block;
  background-color: transparent;
  box-shadow: none;
  border: 0;
  appearance: none;
  padding: 0;
  border-radius: 0;
  color: inherit;
  color: inherit;
  text-rendering: inherit;
  font-weight: inherit;
  line-height: inherit; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }

input[type=number] {
  -moz-appearance: textfield; }

textarea {
  resize: none; }

a, button, label {
  user-select: none;
  -webkit-user-drag: none;
  cursor: pointer; }

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none; }

table {
  border-collapse: collapse; }

a {
  text-decoration: none;
  color: inherit; }

/*========== Layout ==========*/
body {
  min-width: 320px;
  height: 100%; }
  body.login {
    overflow: hidden; }
    body.login .wrapper, body.login .main {
      height: 100%; }

.wrapper {
